.container {
  max-width: 800px;
  text-align: center;
  margin: 0 auto;
  padding: 20px;
}

h1 {
  font-size: 24px;
}

.search-form {
  margin-bottom: 20px;
}

input[type="text"] {
  width: 70%;
  border: 1px solid #ccc;
  border-radius: 4px;
  padding: 8px;
}

button[type="submit"] {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 4px;
  padding: 8px 16px;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  display: flex;
}

.photo-card {
  width: 250px;
  border: 1px solid #ccc;
  border-radius: 4px;
  overflow: hidden;
  box-shadow: 0 4px 6px rgba(0, 0, 0, .1);
}

.photo-card img {
  width: 100%;
  height: auto;
  display: block;
}

.info {
  background-color: #f8f8f8;
  padding: 8px;
}

.info-item {
  margin: 4px 0;
}

.modal {
  width: 100%;
  height: 100%;
  z-index: 1;
  background-color: rgba(0, 0, 0, .7);
  display: none;
  position: fixed;
  top: 0;
  left: 0;
}

.modal-content {
  max-width: 90%;
  max-height: 90%;
  margin: 0 auto;
  display: block;
}

.close {
  color: #fff;
  cursor: pointer;
  font-size: 30px;
  position: absolute;
  top: 10px;
  right: 10px;
}

.info {
  color: #333;
  background-color: #fff;
  padding: 16px;
}

.photo-card:hover {
  transition: transform .3s;
  transform: scale(1.05);
}

.load-more {
  color: #fff;
  cursor: pointer;
  background-color: #3498db;
  border: none;
  border-radius: 5px;
  margin: 20px auto;
  padding: 10px 20px;
  font-size: 16px;
  transition: background-color .3s;
  display: none;
}

.load-more:hover {
  background-color: #2980b9;
}

.gallery {
  flex-wrap: wrap;
  justify-content: space-between;
  display: flex;
}

.photo-card {
  text-align: center;
  border: 1px solid #ddd;
  border-radius: 5px;
  flex: 0 0 calc(25% - 20px);
  margin-bottom: 20px;
  padding: 10px;
}

.photo-card img {
  max-width: 100%;
  height: auto;
}

